import { defineStore } from 'pinia'
import { getCart } from "../service/cart"

// use
let useStore = defineStore("cart", {
    state: () => {
        return {
            cart: 0
        }
    },

    actions: {
        async updateCart(params) {
            const res = await getCart(params)
            this.cart = res.data.length
        },
    }
})

export default useStore